<template>
  <div>
    <da-panel title="大气污染物排放量统计" padding="b" padding-content="tblr" ref="panelCount">
      <da-time-select slot="search" v-model="sataTime" :time-type.sync="timeType" :show-time="[5,8]" @change="changeSataTime"></da-time-select>
      <!-- <da-equal-panel :left-width="null" right-width="460px" height="auto">
        <div slot="left"> -->
      <data-color-lump class="ce_cont_layo inblock  pad-r-10" width="175px" :data-lists="carbonData">
      </data-color-lump>
      <!-- </div>
        <div slot="right"> -->
      <data-color-lump class="inblock ce_cont_layo" width="auto" :data-lists="[comprehensiveEgyCsp]" :bg-color="['#2dc5e3']">
        <template slot-scope="scope">
          <div class="ce-cont-block">
            <h4 class="ce-cont-title">{{scope.data.name}}</h4>
            <p class="ce-cont-data">{{scope.data.data}}</p>
            <p class="text-right ce-cont-unit">{{scope.data.unit}}</p>
          </div>
          <div class="ce-cont-block">
            <h5>同比</h5>
            <p class="ce-cont-time">{{scope.data.yoyTime||"-"}}</p>
            <p class="ce-cont-ratio text-right">{{scope.data.yoyData||"-"}}%<img :src='scope.data.yoyData>0?scope.pic.up:scope.pic.down'></p>
          </div>
          <div class="ce-cont-block">
            <h5>环比</h5>
            <p class="ce-cont-time">{{scope.data.chainTime||"-"}}</p>
            <p class="ce-cont-ratio text-right">{{scope.data.chainData||"-"}}%<img :src='scope.data.yoyData>0?scope.pic.up:scope.pic.down'></p>
          </div>
        </template>
      </data-color-lump>
      <!-- </div>
      </da-equal-panel> -->
      <p class="ce_pad_20">每消耗1吨标煤 = 排放 2.493吨 二氧化碳 + 0.0165吨 二氧化硫 + 0.0156吨 氮氧化合物 + 0.0096吨 烟尘。</p>
    </da-panel>
    <da-panel title="大气污染物排放量趋势图" padding="b" ref="panelChart">
      <div slot="search">
        <da-radio-select v-model="emission" label="name" value-key="value" :option-lists="emissionList" @change="changeEmission"></da-radio-select>
        对比时间：
        <da-time-select v-model="emsStatisTime" :visibleType="false" :time-type.sync="timeType" :show-time="[5,8]" @change="changeEmsStatisTime"></da-time-select>
      </div>
      <!-- <da-line-chart id="airEmsTrend" :date-time="sataTime" label="time" value-key="value" x-axis-key="time" :data-lists="airEmsTrendData" :format="dateFormat" :x-axis-format="xAxisFormat" :legend-format="legendFormat"></da-line-chart> -->
      <da-line-chart id="airEmsTrend" unit="吨" :date-time="sataTime" label="dataTime" value-key="dataValue" x-axis-key="dataTime" :data-lists="airEmsTrendData" :format="dateFormat" :x-axis-format="xAxisFormat" :legend-format="legendFormat"></da-line-chart>
    </da-panel>
    <da-panel title="减少大气污染物排放量统计" padding="b" ref="panelData">
      <div class="ce_flex">
        <data-color-lump class="ce_cont_layo pad-l-10" :data-lists="reduceData" :attr="{name: 'name',value: 'value',unit: 'unit',ratio:''}">
        </data-color-lump>
        <div class="ce_gauge_wrap">
          <da-gauge-chart id="reduceCarbon" title="约减少碳排放量" height="210px" :date-time="reduceCarbontime" :data-lists="reduceCarbonData"></da-gauge-chart>
        </div>
        <div class="ce_cont_wrap">
          <data-color-lump class="ce_cont_layo" width="50%" :data-lists="emissionData" :attr="{name: 'name',value: 'value',unit: 'unit',ratio:''}"></data-color-lump>
        </div>
      </div>
      <div class="ce_w_auto">
        <!-- <p>注：节约 1 度电 = 减排 0.272 千克“碳”</p>
        <p>节约 1 千克原煤 = 减排 0.486 千克“碳”</p> -->
        <p>注：节约 1 吨标准煤 ≈ 减排 0.68 吨“碳”</p>
        <p>节约 1 吨标准煤 ≈ 减排 2.493吨 二氧化碳 + 0.0165吨 二氧化硫 + 0.0156吨 氮氧化合物 + 0.0096吨 烟尘。</p>
      </div>
    </da-panel>
  </div>
</template>
<script>
import dataColorLump from "../analysis/components/dataColorLump";
import { cycleTime, calcTime, reduceById, deepCopy } from "../../../util/util";
export default {
  components: {
    dataColorLump
  },
  data() {
    return {
      dateFormat: "YYYY-MM-DD", //
      xAxisFormat: "DD日", //X轴日期格式化
      legendFormat: "YYYY年MM月",
      yoyBeginTime: "",
      chainBeginTime: "",
      yoyFinishTime: "",
      chaiFinishTime: "",
      sataTime: "",
      timeType: 5,
      comparison: "",
      comparisonList: [],
      energyCost: 0, //综合能耗
      yoyEnergyCost: 0, //同比综合能耗
      chainEnergyCost: 0, //环比综合能耗
      emsStatisTime: "", //
      beginTime: "", //查询的开始时间
      lastBeginTime: "",
      finishTime: "", //查询的结束时间
      lastFinishTime: "",
      carbonData: [
        { name: "二氧化碳(CO2)", value: 0, unit: "吨" },
        { name: "二氧化硫(SO2)", value: 0, unit: "吨" },
        { name: "氮氧化合物(NOX)", value: 0, unit: "吨" },
        { name: "烟尘(Smoke)", value: 0, unit: "吨" }
      ], //碳排放的数据
      comprehensiveEgyCsp: {
        name: "综合能耗",
        yoyTime: "",
        unit: "吨标准煤",
        data: 0,
        yoyData: "",
        chainTime: "",
        chainData: ""
      }, //综合能耗
      comprehensiveEgySave: 0, //综合节能量
      emissionList: [
        {
          name: "二氧化碳(CO₂)",
          value: 0
        },
        {
          name: "二氧化硫(SO₂)",
          value: 1
        },
        {
          name: "氮氧化合物(NOX)",
          value: 2
        },
        {
          name: "烟尘(Smoke)",
          value: 3
        }
      ], //排放元素的种类列表
      emission: 0, //排放元素的种类
      emissionName: "",
      airEmsTrendTime: "", //碳排放趋势分析时间
      airEmsTrendData: [], //碳排放趋势分析
      airEmsData: [],
      contrastAirEmsData: [],
      reduceCarbontime: "", //时间
      reduceData: [
        { name: "综合节能量", value: 0, unit: "吨" },
        { name: "约减少碳排放", value: 0, unit: "吨" }
      ],
      reduceCarbonData: {
        min: -6800,
        max: 68000,
        value: 0,
        unit: "吨(t)"
      }, //减排数据
      emissionData: [
        { name: "二氧化碳(CO₂)", value: 0, unit: "吨" },
        { name: "二氧化硫(SO₂)", value: 0, unit: "吨" },
        { name: "氮氧化合物(NOX)", value: 0, unit: "吨" },
        { name: "烟尘(Smoke)", value: 0, unit: "吨" }
      ], //排放物数据
      bgColor: ["#548630", "#54ddde", "#456dca", "#a578d3"] //色块样式配置
    };
  },
  methods: {
    //初始化参数
    inintData() {
      //设置默认时间
      let arrayTime1 = cycleTime(new Date(), 5);
      this.sataBeginTime = arrayTime1[0];
      this.sataFinishTime = arrayTime1[1];
      let time = calcTime(new Date(), -1, 5, "YYYY-MM");
      let arrayTime2 = cycleTime(time, 5);
      this.emsStatisTime = arrayTime2[0];
      this.lastBeginTime = arrayTime2[0];
      this.lastFinishTime = arrayTime2[1];
    },
    //页面加载数据
    loadData() {
      this.getEmissionData();
      let targetCycle = this.$store.state.targetConstant.targetCycle.day;
      this.getAirEmsTrendData(targetCycle);
      this.getSaveEnergyTarget();
    },
    //切换查询时间
    changeSataTime() {
      let time2 = [];
      let time3 = [];
      if (this.timeType == 5) {
        this.dateFormat = "YYYY-MM";
        this.xAxisFormat = "DD日"; //X轴日期格式化
        this.legendFormat = "YYYY年MM月";
        let time1 = cycleTime(this.sataTime, 5);
        this.sataBeginTime = time1[0];
        this.sataFinishTime = time1[1];
        time2 = cycleTime(calcTime(this.sataTime, -1, 5, "YYYY-MM-DD"), 5);
        time3 = cycleTime(calcTime(this.sataTime, -1, 8, "YYYY-MM-DD"), 5);
        this.yoyBeginTime = time2[0];
        this.yoyFinishTime = time2[1];
        this.chainBeginTime = time3[0];
        this.chaiFinishTime = time3[1];
        this.comprehensiveEgyCsp.chainTime = calcTime(
          this.sataTime,
          -1,
          5,
          "YYYY年MM月"
        );
        this.comprehensiveEgyCsp.yoyTime = calcTime(
          this.sataTime,
          -12,
          5,
          "YYYY年MM月"
        );
        let time4 = cycleTime(this.emsStatisTime, 5);
        this.lastBeginTime = time4[0];
        this.lastFinishTime = time4[1];
      } else {
        this.dateFormat = "YYYY";
        this.xAxisFormat = "MM月"; //X轴日期格式化
        this.legendFormat = "YYYY年";
        let time1 = cycleTime(this.sataTime, 8);
        this.sataBeginTime = time1[0];
        this.sataFinishTime = time1[1];
        time2 = cycleTime(calcTime(this.sataTime, -12, 5, "YYYY-MM-DD"), 8);
        this.yoyBeginTime = this.chainBeginTime = time2[0];
        this.yoyFinishTime = this.chaiFinishTime = time2[1];
        this.comprehensiveEgyCsp.yoyTime = this.comprehensiveEgyCsp.chainTime = calcTime(
          this.sataTime,
          -1,
          8,
          "YYYY年"
        );
        let time3 = cycleTime(this.emsStatisTime, 8);
        this.lastBeginTime = time3[0];
        this.lastFinishTime = time3[1];
      }
      this.getEmissionData();
      let timeCycle = this.timeType == 5 ? 3 : 5;
      this.getAirEmsTrendData(timeCycle);
      this.getSaveEnergyTarget();
    },
    //大气污染物排放量统计
    getEmissionData() {
      let orgId = this.$store.state.targetConstant.orgId.enterprise;
      let targetType = this.$store.state.targetConstant.targetType.energyConsum;
      let contentTypes = this.$store.state.targetConstant.targetContentType
        .energy;
      let energyIds = this.$store.state.targetConstant.energy
        .compreEnergyConsum;
      this.$ajax({
        url: "rest/analysisData/targetData",
        method: "get",
        params: {
          title: "实际综合能耗量", // String,功能标题
          orgIds: orgId, // String,部门ID
          targetCycle: this.timeType, // String,指标周期
          targetTypes: targetType, // String,指标类型
          contentTypes: contentTypes, // String,统计内容类型
          contentIds: energyIds, // String,统计内容ID
          objTypes: null, // String,指标关联对象类型
          objTypeValues: null, // String,指标关联对象值
          isQueryAll: false, // Boolean,是否所有指标数据
          startTime: this.sataBeginTime, // String,开始时间
          endTime: this.sataFinishTime, // String,结束时间
          isOrgId: false, // Boolean,是否返回部门ID
          isContentType: true, // Boolean,是否返回指标统计内容类型ID
          isObjType: null // String,是否返回指标关联对象,1为班组，2为设备，3为产品
        }
      }).then(res => {
        if (res.data.status) {
          let sum = 0;
          for (let i = 0; i < res.data.records.length; i++) {
            sum += res.data.records[i].dataValue;
          }
          this.comprehensiveEgyCsp.data = this.energyCost = sum / 1000;
          this.comprehensiveEgyCsp.data = parseFloat(
            this.comprehensiveEgyCsp.data
          ).toFixed(2);
          this.carbonData[0].value = (this.energyCost * 2.493).toFixed(2);
          this.carbonData[1].value = (this.energyCost * 0.0165).toFixed(2);
          this.carbonData[2].value = (this.energyCost * 0.0156).toFixed(2);
          this.carbonData[3].value = (this.energyCost * 0.0096).toFixed(2);
          this.getYoyEmissionData();
          this.getChainEmissionData();
        }
      });
    },
    //切换大气污染物排放趋势图
    changeEmission() {
      let timeCycle = this.timeType == 5 ? 3 : 5;
      this.getAirEmsTrendData(timeCycle);
    },
    //切换对比时间时调用方法
    changeEmsStatisTime() {
      let time = [];
      if (this.timeType == 5) {
        time = cycleTime(this.emsStatisTime, 5);
      } else {
        time = cycleTime(this.emsStatisTime, 8);
      }
      this.lastBeginTime = time[0];
      this.lastFinishTime = time[1];
      let timeCycle = this.timeType == 5 ? 3 : 5;
      this.getAirEmsTrendData(timeCycle);
    },
    //同比综合能耗
    getYoyEmissionData() {
      let orgId = this.$store.state.targetConstant.orgId.enterprise;
      let targetType = this.$store.state.targetConstant.targetType.energyConsum;
      let contentTypes = this.$store.state.targetConstant.targetContentType
        .energy;
      let energyIds = this.$store.state.targetConstant.energy
        .compreEnergyConsum;
      this.$ajax({
        url: "rest/analysisData/targetData",
        method: "get",
        params: {
          title: "同比综合能耗", // String,功能标题
          orgIds: orgId, // String,部门ID
          targetCycle: this.timeType, // String,指标周期
          targetTypes: targetType, // String,指标类型
          contentTypes: contentTypes, // String,统计内容类型
          contentIds: energyIds, // String,统计内容ID
          objTypes: null, // String,指标关联对象类型
          objTypeValues: null, // String,指标关联对象值
          isQueryAll: false, // Boolean,是否所有指标数据
          startTime: this.yoyBeginTime, // String,开始时间
          endTime: this.yoyFinishTime, // String,结束时间
          isOrgId: false, // Boolean,是否返回部门ID
          isContentType: true, // Boolean,是否返回指标统计内容类型ID
          isObjType: null // String,是否返回指标关联对象,1为班组，2为设备，3为产品
        }
      }).then(res => {
        if (res.data.status) {
          this.yoyEnergyCost = res.data.records;
          let sum = 0;
          for (let i = 0; i < res.data.records.length; i++) {
            sum += res.data.records[i].dataValue;
          }
          this.yoyEnergyCost = sum / 1000;
          let num = this.energyCost - this.yoyEnergyCost;
          if (this.yoyEnergyCost < 0 || this.yoyEnergyCost > 0) {
            //同比计算,增长百分比
            this.comprehensiveEgyCsp.yoyData = (
              num /
              this.yoyEnergyCost *
              100
            ).toFixed(2);
          } else {
            this.comprehensiveEgyCsp.yoyData = "-";
          }
        }
      });
    },
    //环比综合能耗
    getChainEmissionData() {
      let orgId = this.$store.state.targetConstant.orgId.enterprise;
      let targetType = this.$store.state.targetConstant.targetType.energyConsum;
      let contentTypes = this.$store.state.targetConstant.targetContentType
        .energy;
      let energyIds = this.$store.state.targetConstant.energy
        .compreEnergyConsum;
      this.$ajax({
        url: "rest/analysisData/targetData",
        method: "get",
        params: {
          title: "环比综合能耗", // String,功能标题
          orgIds: orgId, // String,部门ID
          targetCycle: this.timeType, // String,指标周期
          targetTypes: targetType, // String,指标类型
          contentTypes: contentTypes, // String,统计内容类型
          contentIds: energyIds, // String,统计内容ID
          objTypes: null, // String,指标关联对象类型
          objTypeValues: null, // String,指标关联对象值
          isQueryAll: false, // Boolean,是否所有指标数据
          startTime: this.chainBeginTime, // String,开始时间
          endTime: this.chaiFinishTime, // String,结束时间
          isOrgId: false, // Boolean,是否返回部门ID
          isContentType: true, // Boolean,是否返回指标统计内容类型ID
          isObjType: null // String,是否返回指标关联对象,1为班组，2为设备，3为产品
        }
      }).then(res => {
        if (res.data.status) {
          this.chainEnergyCost = res.data.records;
          let sum = 0;
          for (let i = 0; i < res.data.records.length; i++) {
            sum += res.data.records[i].dataValue;
          }
          this.chainEnergyCost = sum / 1000;
          let num = this.energyCost - this.chainEnergyCost;
          if (this.chainEnergyCost < 0 || this.chainEnergyCost > 0) {
            //环比计算，增长百分比
            this.comprehensiveEgyCsp.chainData = (
              num /
              this.chainEnergyCost *
              100
            ).toFixed(2);
          } else {
            this.comprehensiveEgyCsp.chainData = "-";
          }
        }
      });
    },
    //获得节能减排数据计算
    getSaveEnergyTarget() {
      let orgId = this.$store.state.targetConstant.orgId.enterprise;
      let targetType = this.$store.state.targetConstant.targetType.saveEnergy;
      let contentTypes = this.$store.state.targetConstant.targetContentType
        .energy;
      let energyIds = this.$store.state.targetConstant.energy
        .compreEnergyConsum;

      this.$ajax({
        url: "rest/analysisData/targetData",
        method: "get",
        params: {
          title: "实际节能能耗", // String,功能标题
          orgIds: orgId, // String,部门ID
          targetCycle: this.timeType, // String,指标周期
          targetTypes: targetType, // String,指标类型
          contentTypes: contentTypes, // String,统计内容类型
          contentIds: energyIds, // String,统计内容ID
          objTypes: null, // String,指标关联对象类型
          objTypeValues: null, // String,指标关联对象值
          isQueryAll: false, // Boolean,是否所有指标数据
          // benchmarkQuota: "001", //长度为3的字符串"111"表示是否查询[标杆][定额][目标]
          startTime: this.sataBeginTime, // String,开始时间
          endTime: this.sataFinishTime, // String,结束时间
          isOrgId: false, // Boolean,是否返回部门ID
          isContentType: false, // Boolean,是否返回指标统计内容类型ID
          isObjType: null // String,是否返回指标关联对象,1为班组，2为设备，3为产品
        }
      }).then(res => {
        if (res.data.status) {
          //统计目标节能量
          let sum = 0;
          for (let i = 0; i < res.data.records.length; i++) {
            sum += res.data.records[i].dataValue;
          }
          //节能指标统计
          // this.comprehensiveEgySave = sum.toFixed(2);
          //表盘数据处理
          let num = parseFloat((sum * 0.68).toFixed(2));
          this.reduceCarbonData = {
            min: -6800,
            max: 6800,
            value: num,
            unit: "吨(t)"
          };
          //综合节能量
          this.reduceData[0].value = sum.toFixed(2);
          //减少碳排放
          this.reduceData[1].value = (sum * 0.68).toFixed(2);
          //统计各种排放物
          this.emissionData[0].value = (sum * 2.493).toFixed(2);
          this.emissionData[1].value = (sum * 0.0165).toFixed(2);
          this.emissionData[2].value = (sum * 0.0156).toFixed(2);
          this.emissionData[3].value = (sum * 0.0096).toFixed(2);
        }
      });
    },
    //大气污染物排放量趋势图
    getAirEmsTrendRequest(timeCycle, startTime, endTime) {
      let orgId = this.$store.state.targetConstant.orgId.enterprise;
      let targetType = this.$store.state.targetConstant.targetType.energyConsum;
      let contentTypes = this.$store.state.targetConstant.targetContentType
        .energy;
      let energyIds = this.$store.state.targetConstant.energy
        .compreEnergyConsum;

      return this.$ajax({
        url: "rest/analysisData/targetData",
        method: "get",
        params: {
          title: "大气污染物排放趋势图", // String,功能标题
          orgIds: orgId, // String,部门ID
          targetCycle: timeCycle, // String,指标周期
          targetTypes: targetType, // String,指标类型
          contentTypes: contentTypes, // String,统计内容类型
          contentIds: energyIds, // String,统计内容ID
          objTypes: null, // String,指标关联对象类型
          objTypeValues: null, // String,指标关联对象值
          isQueryAll: false, // Boolean,是否所有指标数据
          startTime: startTime, // String,开始时间
          endTime: endTime, // String,结束时间
          isOrgId: false, // Boolean,是否返回部门ID
          isContentType: true, // Boolean,是否返回指标统计内容类型ID
          isObjType: null // String,是否返回指标关联对象,1为班组，2为设备，3为产品
        }
      });
    },
    getAirEmsTrendData(timeCycle) {
      this.$ajax
        .all([
          this.getAirEmsTrendRequest(
            timeCycle,
            this.sataBeginTime,
            this.sataFinishTime
          ),
          this.getAirEmsTrendRequest(
            timeCycle,
            this.lastBeginTime,
            this.lastFinishTime
          )
        ])
        .then(res => {
          this.emissionName = this.emissionList[this.emission].name;
          let num = 0;
          switch (this.emission) {
            case 0:
              num = 2.493;
              break;
            case 1:
              num = 0.0165;
              break;
            case 2:
              num = 0.0156;
              break;
            case 3:
              num = 0.0096;
              break;
          }

          if (res[0].data.records == null) {
            res[0].data.records == [];
          }
          if (res[1].data.records == null) {
            res[1].data.records == [];
          }
          let tempData = res[0].data.records.concat(res[1].data.records);
          let data = tempData.map(item => {
            let d = deepCopy(item);
            d.dataValue = (d.dataValue * num / 1000).toFixed(2);
            d.targetUnit = "tec";
            return d;
          });
          this.airEmsTrendData = data;
        });
    }
  },
  mounted() {
    this.inintData();
    this.loadData();
  }
};
</script>

<style scoped>
.ce_25p {
  width: 25%;
  display: inline-block;
}
/* .ce_pad_20 {
 padding-top: 15px;
} */

.ce_flex {
  display: flex;
  align-items: flex-end;
}
.ce_gauge_wrap {
  display: inline-block;
  /* width: 400px; */
  flex: 1;
  padding: 10px;
}
.ce_w_auto {
  display: inline-block;
  /* width: 270px; */
  padding-left: 10px;
  line-height: 30px;
}
.ce_cont_wrap {
  /* flex: 1; */
  width: 450px;
  display: inline-block;
}

/* .ce-cont-block {
  margin: 10px 0;
  display: inline-block;
  height: 100%;
  background-color: #2dc5e3;
  padding: 10px 10px 9px;
  color: #fff;
} */

.ce-cont-block {
  min-width: 100px;
  display: inline-block;
  padding: 0 15px;
}
.ce_cont_layo {
  vertical-align: top;
  padding-bottom: 10px;
}

.ce-cont-title {
  font-size: 18px;
}
.ce-cont-block h5 {
  font-size: 16px;
}
.ce-cont-data,
.ce-cont-ratio {
  font-size: 24px;
  font-weight: bold;
  padding: px 0px;
  text-align: center;
  color: #ebc85e;
}
.ce-cont-ratio {
  font-size: 18px;
}
.ce-cont-time {
  text-align: center;
  padding: 5px 0;
  font-size: 15px;
}

.ce-cont-unit {
  font-size: 14px;
}

@media screen and (max-width: 1200px) {
  .ce_flex {
    display: block;
  }
  .ce_gauge_wrap,
  .ce_cont_wrap {
    width: 100%;
    padding: 10px;
  }
  .ce_cont_wrap {
    display: block;
  }
}
</style>



