<template>
    <carbon-data class="ce_cont_layo inblock  pad-r-10" :width="width" :data-lists="dataLists" :bg-color="bgColor" :attr="attr">
        <template slot-scope="scope">
            <slot slot-scope="scope" :data="scope.data" :pic="scope.pic">
                <h4 class="ce-cont-title text-left">{{scope.data.name}}</h4>
                <p class="ce-cont-data">{{scope.data.value}}</p>
                <p class="ce-cont-unit text-right">{{scope.data.unit}}</p>
            </slot>
        </template>
    </carbon-data>
</template>

<script>
import carbonData from "./carbonData.vue";
export default {
  components: {
    carbonData
  },
  props: {
    width: { type: String },
    dataLists: { type: Array },
    bgColor: { type: [String, Array] },
    attr: { type: Object }
  }
};
</script>
<style scoped>
.ce_cont_layo {
  vertical-align: top;
  padding-bottom: 10px;
}

.ce-cont-title {
  font-size: 18px;
}
.ce-cont-block h5 {
  font-size: 16px;
}
.ce-cont-data,
.ce-cont-ratio {
  font-size: 24px;
  font-weight: bold;
  padding: px 0px;
  text-align: center;
  color: #ebc85e;
}
.ce-cont-ratio {
  font-size: 18px;
}
.ce-cont-time {
  text-align: center;
  padding: 5px 0;
  font-size: 15px;
}

.ce-cont-unit {
  font-size: 14px;
}
</style>



